<template>
    <div class="cropper">
        <div>
            <span class="button close" @click="handlerClose">返回</span>
            <span class="button confirm" @click="handlerConfirm">确定</span>
        </div>
        <v-cropper ref="cropper" style="background-image: none; background-color: #000" outputType="png"
                   :img="img"
                   :info="false"
                   :auto-crop-width="200"
                   :auto-crop-height="200"
                   :can-move-box="false"
                   auto-crop fixed-box original center-box full/>
    </div>
</template>

<script>
    import VCropper from "vue-cropper/src/vue-cropper"

    export default {
        name: "cropper-component",
        props: {
            value: {},
            img: {
                type: String,
                default: ''
            }
        },
        methods: {
            handlerClose() {
                this.$emit('input', false)
                this.$emit('close', false)
            },
            handlerConfirm() {
                this.$refs.cropper.getCropData(data => {
                    this.$emit('confirm', data)
                    this.handlerClose()
                })
            }
        },
        components: {
            VCropper
        }
    }
</script>

<style lang="stylus" scoped>
    .cropper
        position absolute
        top 0
        left 0
        right 0
        bottom 0
        z-index 10000
        .button
            position absolute
            top 0
            z-index 100
            padding 20px 30px
            color #fff
            &.close
                left 0
            &.confirm
                right 0
</style>



