<template>
    <div class="register">
        <navigate :backText="step[currentStep].backText" :title="step[currentStep].title" @back="onBack"
                  back backgroundColor="#3899D7"
                  color="#fff" position="absolute"/>
        <div class="register-form">
            <!-- 第一步 -->
            <div class="step-1-form" v-show="currentStep === 0">
                <div class="form-item paper mt-20">
                    <div class="form-item-label" style="width: 65px">手机号码</div>
                    <mu-text-field full-width placeholder="请输入您的手机号" solo v-model="form.phone"/>
                </div>
                <div class="form-button">
                    <mu-button :disabled="validateFirst !== '获取手机验证码'" @click="next"
                               class="button mu-primary-color bg"
                               color="#fff"
                               flat large>{{validateFirst}}
                    </mu-button>
                </div>
                <div class="agree">
                    <mu-checkbox v-model="form.agree"/>
                    <span @click="form.agree = !form.agree" class="theme-text-secondary">我已阅读并同意
                        <a @click.stop="agree.visible = true" class="agreement">《广东航道通移动客户端用户使用协议》</a>
                    </span>
                </div>
            </div>
            <!-- 第一步 -->

            <!-- 第二步 -->
            <div class="step-2-form" v-show="currentStep === 1">
                <div class="head-img">
                    <mu-avatar @click="handlerClickAvatar" size="80" title="点击添加">
                        <img :key="cropper.avatar" v-lazy="AvatarImage(cropper.avatar)">
                    </mu-avatar>
                </div>
                <div class="form-item paper border-1px-tb">
                    <div class="form-item-label">验证码</div>
                    <mu-text-field full-width placeholder="4位数短信验证码" solo type="number" v-model="form.code"/>
                    <mu-button :disabled="validate.disabled" @click="getValidate"
                               class="code-btn bg"
                               color="primary"
                               flat>{{validate.text}}
                    </mu-button>
                </div>
                <div class="form-item paper border-1px-b">
                    <div class="form-item-label">昵称</div>
                    <mu-text-field :action-click="() => {this.form.alias = ''}" :action-icon="form.alias ? 'close' : ''"
                                   full-width
                                   placeholder="请输入昵称"
                                   solo v-model="form.alias"/>
                </div>
                <div class="form-item paper border-1px-b">
                    <div class="form-item-label">密码</div>
                    <mu-text-field :action-click="() => {this.visibility = !this.visibility}"
                                   :action-icon="visibility ? 'visibility' : 'visibility_off'"
                                   :type="visibility ? 'message.vue' : 'password'"
                                   full-width
                                   placeholder="6-20位字母、数字和符号"
                                   solo v-model="form.password"/>
                </div>
                <div class="form-button">
                    <mu-button :disabled="loading || validateLast !== '立即注册'" @click="onRegister"
                               class="button mu-primary-color bg"
                               color="#fff"
                               data-mu-loading-size="24"
                               flat
                               large v-loading="loading">
                        {{validateLast}}
                    </mu-button>
                </div>
            </div>
            <!-- 第二步 -->
        </div>

        <!-- 头像裁剪 -->
        <cropper :img="form.avatar" @confirm="handlerCropperConfirm" v-model="cropper.visible"
                 v-show="cropper.visible"/>
        <!-- 头像裁剪 -->

        <div style="visibility: hidden">
            <input @change="fileChange" ref="file" type="file">
        </div>

        <!-- 协议 -->
        <mu-dialog :open.sync="agree.visible" lock-scroll max-width="86%"
                   scrollable
                   title="广东航道通移动客户端用户使用协议"
                   width="500px">
            <service-agreement/>
        </mu-dialog>
        <!-- 协议 -->
    </div>
</template>

<script>
    import Navigate from "../../base/navigate/navigate"
    import Cropper from '../../base/cropper/cropper'
    import ServiceAgreement from '../../components/agreement/service-agreement'
    import {userMixins} from "../../common/mixins"
    import {checkPhone, getValidate, register} from "../../api/user"
    import {uploadBase64} from "../../api/sys"
    import {validate} from "../../types/index"

    export default {
        name: "register",
        mixins: [userMixins],
        data() {
            return {
                currentStep: 0,
                loading: false,
                agree: {
                    visible: false
                },
                step: [
                    {title: '填写手机号码', backText: ''},
                    {title: '填写账号信息', backText: '上一步'}
                ],
                validate: {
                    phone: '',
                    type: 1,
                    timer: null,
                    disabled: false,
                    text: '获取验证码'
                },
                visibility: false,
                form: {
                    phone: '',
                    agree: false,
                    code: '',
                    alias: '',
                    password: '',
                    avatar: ''
                },
                cropper: {
                    visible: false,
                    avatar: ''
                }
            };
        },
        computed: {
            //  第一步验证
            validateFirst() {
                const phone = /^((13[0-9])|(14[5,7,9])|(15[^4])|(18[0-9])|(17[0,1,3,5,6,7,8]))\d{8}$/.test(this.form.phone)
                const agree = this.form.agree
                if (!phone) {
                    return '手机号码不正确'
                }
                if (!agree) {
                    return '请阅读并同意服务协议'
                }

                return '获取手机验证码'
            },
            //  第二部验证
            validateLast() {
                const code = /^\d{4}$/.test(this.form.code)
                const alias = this.form.alias
                const password = /^[a-zA-Z\d]{6,16}$/.test(this.form.password)

                if (!code) {
                    return '请输入正确的验证码'
                }

                if (!alias) {
                    return '请输入昵称'
                }

                if (!this.form.password) {
                    return '密码不能为空'
                }

                if (!password) {
                    return '请输入6-16位密码，不支持特殊符号'
                }

                return '立即注册'
            }
        },
        activated() {
            this.form = {
                phone: '',
                agree: false,
                code: '',
                alias: '',
                password: '',
                avatar: ''
            }

            this.currentStep = 0
        },
        methods: {
            // 返回上一步
            onBack() {
                if (this.currentStep === 1) {
                    this.form = {phone: '', agree: false, code: '', alias: '', password: '', avatar: ''}

                    this.cropper.avatar = ''

                    this.currentStep--
                } else {
                    this.$router.back()
                }
            },
            // 下一步
            next() {
                if (this.currentStep === 0) {
                    checkPhone(this.form.phone).then(res => {
                        this.currentStep++

                        !this.validate.disabled && this.getValidate()
                    })
                }
            },
            //  选择头像
            handlerClickAvatar() {
                this.$refs.file.click()
            },
            fileChange(event) {
                let file = event.target.files[0]

                if (!/\.(gif|jpg|jpeg|png|bmp|GIF|JPG|PNG)$/.test(event.target.value)) {
                    alert('图片类型必须是.gif,jpeg,jpg,png,bmp中的一种')
                    return false
                }
                const reader = new FileReader()

                reader.onload = e => {
                    let data

                    if (typeof e.target.result === 'object') {
                        // 把Array Buffer转化为blob 如果是base64不需要
                        data = window.URL.createObjectURL(new Blob([e.target.result]))
                    } else {
                        data = e.target.result
                    }

                    this.form.avatar = data

                    this.cropper.visible = true
                }
                // 转化为base64
                // reader.readAsDataURL(file)
                // 转化为blob
                reader.readAsArrayBuffer(file)
            },
            // 上传头像
            handlerCropperConfirm(data) {
                this.cropper.avatar = data
            },
            // 获取验证码
            getValidate() {
                getValidate(this.validate.type, this.form.phone).then(res => {
                    validate(this.validate)
                })
            },
            // 点击注册
            async onRegister() {
                this.loading = true
                if (this.cropper.avatar && typeof this.cropper.avatar === 'string') {
                    this.$toast({
                        position: 'center',
                        type: 'loading',
                        message: '正在上传头像 ...',
                        duration: 0
                    })

                    this.form.avatar = await uploadBase64(this.cropper.avatar)
                }

                this.$toast({
                    position: 'center',
                    type: 'loading',
                    message: '正在注册 ...',
                    duration: 0
                })

                const data = {
                    phone: this.form.phone,
                    pwd: this.form.password,
                    alias: this.form.alias,
                    code: this.form.code,
                    portrait: this.form.avatar
                }

                register(data).then(res => {
                    this.$router.replace('/login')

                    this.loading = false
                    this.$toast(res.msg)
                }).catch(err => {
                    console.log(err)

                    this.loading = false
                    this.$toast.close()
                })
            }
        },
        watch: {
            'form.phone'(value) {
                this.validate.phone = value

                validate(this.validate, true)
            }
        },
        components: {
            Navigate, Cropper, ServiceAgreement
        }
    }
</script>

<style lang="stylus" scoped>
    @import '../../common/stylus/variable.styl';

    .service-agreement
        line-height 1.74625
        font-size 14px
        font-weight 300
        text-align justify
        text-indent 2em

    .register
        padding-top 46px
        width 100%
        height 100%
        background-color #efefef
        overflow hidden

        .register-form
            .form-item
                background-color #fff
                padding 0 15px
                display flex
                align-items center

                &.mt-20
                    margin-top 20px

                >>> .mu-input-action-icon
                    color #cecece

                .form-item-label
                    color #3899D7
                    width 60px
                    margin-right 15px
                    height 48px
                    line-height 48px
                    text-align justify

                    &:after
                        content ""
                        display inline-block
                        width 100%

                .mu-input
                    flex 1

                .code-btn
                    width 110px
                    border-radius 6px

                    >>> .mu-button-wrapper
                        padding 0
                        font-size 12px

            .form-button
                padding 20px 10px

                .button
                    width 100%

            .agree
                padding 0 15px
                font-size $font-size-ms
                line-height 30px

                .agreement
                    color #3899D7

                .mu-checkbox
                    transform translate(0, 6px)
                    margin-left 10px

            .head-img
                text-align center
                padding 40px 0
</style>



